"use client";

import * as React from "react";

type SidebarContextProps = {
  active: string;
  setActive: (key: string) => void;
};

const SidebarContext = React.createContext<SidebarContextProps | null>(null);

export function useSidebar() {
  const ctx = React.useContext(SidebarContext);
  if (!ctx) throw new Error("useSidebar must be inside SidebarProvider");
  return ctx;
}

export function Sidebar({ children }: { children: React.ReactNode }) {
  const [active, setActive] = React.useState("");
  return (
    <SidebarContext.Provider value={{ active, setActive }}>
      <aside className="bg-gray-100 border-r w-64 h-screen flex flex-col">
        {children}
      </aside>
    </SidebarContext.Provider>
  );
}

export function SidebarHeader({
  className,
  ...props
}: React.ComponentProps<"div">) {
  return <div className={cn("p-2 border-b", className)} {...props} />;
}

export function SidebarContent({
  className,
  ...props
}: React.ComponentProps<"div">) {
  return <div className={cn("flex-1 overflow-auto", className)} {...props} />;
}

export function SidebarFooter({
  className,
  ...props
}: React.ComponentProps<"div">) {
  return <div className={cn("border-t p-2", className)} {...props} />;
}

export function SidebarGroup({
  className,
  ...props
}: React.ComponentProps<"div">) {
  return <div className={cn("p-2", className)} {...props} />;
}

export function SidebarGroupLabel({
  className,
  ...props
}: React.ComponentProps<"div">) {
  return (
    <div className={cn("text-xs font-semibold text-gray-500 mb-1", className)} {...props} />
  );
}

export function SidebarGroupContent({
  className,
  ...props
}: React.ComponentProps<"div">) {
  return <div className={cn("flex flex-col gap-1", className)} {...props} />;
}

export function SidebarMenu({
  className,
  ...props
}: React.ComponentProps<"ul">) {
  return <ul className={cn("flex flex-col gap-1", className)} {...props} />;
}

export function SidebarMenuItem({
  className,
  ...props
}: React.ComponentProps<"li">) {
  return <li className={cn("w-full", className)} {...props} />;
}

export function SidebarMenuButton({
  isActive,
  className,
  ...props
}: React.ComponentProps<"button"> & { isActive?: boolean }) {
  return (
    <button
      data-active={isActive}
      className={cn(
        "flex items-center gap-2 w-full rounded-md px-2 py-1 text-sm hover:bg-gray-200 data-[active=true]:bg-gray-300 font-medium cursor-pointer",
        className
      )}
      {...props}
    />
  );
}
